<template>
  <div ref="aniViewDemo1"></div>
</template>

<script>
import * as THREE from 'three'
export default {
  name: 'aniDemo1',
  metaInfo: {
    title: '',
    meta: [
      {charset: 'utf-8'},
      {name: 'keywords', content: ''},
      {name: 'description', content: ''},
      {name: 'viewport', content: 'width=device-width,initial-scale=1,minimum-scale=1,maximum-scale=1,user-scalable=no'}
    ]
  },
  data () {
    return {
      scene: null, // 
      geometry: null, // 
      material: null, // 
      mesh: null, // 
      point: null, // 
      ambient: null, // 
      camera: null, // 
      renderer: null, // 
      firstTime: new Date() // 
    }
  },
  created () {
    /**
     *  Scene
     */
    this.scene = new THREE.Scene()
    /**
     * 
     */
    // Geometry
    this.geometry = new THREE.BoxGeometry(100, 100, 100)
    //  Material
    this.material = new THREE.MeshLambertMaterial({
      color: 0x0000ff
    })
    //  Mesh
    this.mesh = new THREE.Mesh(this.geometry, this.material)
    // 
    this.scene.add(this.mesh)
    /**
     * 
     */
    // 
    this.point = new THREE.PointLight(0xffffff)
    // 
    this.point.position.set(400, 200, 300)
    // 
    this.scene.add(this.point)
    // 
    this.ambient = new THREE.AmbientLight(0x444444)
    this.scene.add(this.ambient)
    /**
     * 
     */
    // 
    let width = window.innerWidth
    // 
    let height = window.innerHeight
    // 
    let kgb = width / height
    // 
    let num = 200
    /**
     * 
     */
    this.camera = new THREE.OrthographicCamera(-num * kgb, num * kgb, num, -num, 1, 1000)
    // 
    this.camera.position.set(200, 300, 200)
    // ()
    this.camera.lookAt(this.scene.position)
    /**
     * 
     */
    this.renderer = new THREE.WebGLRenderer()
    // 
    this.renderer.setSize(width, height)
    // 
    this.renderer.setClearColor(0xb9d3ff, 1)
  },
  methods: {
    render: function () {
      // 
      let newTime = new Date()
      // 
      let sjcTime = newTime - this.firstTime
      // 
      this.firstTime = newTime
      //
      window.requestAnimationFrame(this.render)
      //  
      this.renderer.render(this.scene, this.camera)
      //  0.001
      this.mesh.rotateY(0.001 * sjcTime)
    }
  },
  mounted () {
    //  canvas 
    this.$refs.aniViewDemo1.appendChild(this.renderer.domElement)
    //
    this.render()
  }
}
</script>

<style scoped>
  html,body{
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
</style>



